@charset "UTF-8";

.wrapper {
  max-width: 1280px;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
}

html,
body {
  background: #fff;
  color: #000;
}

body,
input,
select,
textarea,
button {
  font-family: "CenturyGothic", sans-serif;
  font-size: 100%;
  line-height: 1.2;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  overflow-x: hidden;
}

img {
  width: 100%;
  height: auto;
}

.section-title {
  margin: 0;
  padding: 0;
  font-size: 40px;
  font-weight: normal;
  text-transform: uppercase;
}

.btn {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
  font-size: 20px;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
}

.btn-danger {
  background-color: #fb3840;
  color: #fff;
}

.btn-danger:hover,
.btn-danger:focus,
.btn-danger:active {
  opacity: 0.8;
}

.btn-light {
  background-color: #fff;
  color: #000;
}

.btn-light:hover,
.btn-light:focus,
.btn-light:active {
  color: #fb3840;
}

.hero {
  background: url(../images/bkg-01.jpg) no-repeat center center;
  background-size: cover;
  height: 1130px;
}

.hero .wrapper {
  height: 1130px;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-end;
}

.hero-content {
  display: flex;
  flex-flow: column wrap;
  align-items: flex-end;
  max-width: 694px;
  height: 100%;
  margin-right: 48px;
  padding-left: 20px;
  padding-right: 40px;
  background-color: rgba(255, 255, 255, 0.9);
}

.contacts a {
  display: inline-block;
  padding-top: 5px;
  padding-bottom: 5px;
  font-size: 22px;
  color: #151515;
  text-decoration: none;
}

.contacts a:hover,
.contacts a:focus,
.contacts a:active {
  color: #fb3840;
}

.contacts a:not(:last-child) {
  margin-right: 80px;
}

.hero-logos {
  margin-top: 30px;
  margin-bottom: 112px;
}

.hero-logo {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 10px;
}

.hero-logo--1 {
  display: block;
  margin-right: 40px;
  max-width: 200px;
  min-width: 100px;
}

.hero-logo--2 {
  min-width: 100px;
  max-width: 200px;
  display: block;
}

.hero-logos p {
  margin: 0;
  padding: 0;
  font-size: 18px;
  text-align: right;
  color: #231f20;
}

.hero-heading {
  text-align: right;
  margin-bottom: 100px;
  max-width: 100%;
}

.hero-heading__title {
  margin: 0;
  margin-bottom: 24px;
  padding: 0;
  max-width: 100%;
  font-size: 32px;
  font-weight: normal;
  text-transform: uppercase;
}

.hero-heading__desc {
  margin: 0;
  padding: 0;
  font-size: 24px;
}

.hero-price {
  max-width: 530px;
  padding: 40px 28px 30px 28px;
  border: 1.5px solid #242021;
  font-size: 0;
  text-align: center;
}

.price-title {
  margin-bottom: 28px;
  font-size: 28px;
  text-align: center;
}

.hero-price .btn {
  width: 200px;
}

.hero-price .btn:not(:last-child) {
  margin-right: 20px;
}

#small-modal {
  padding: 2em;
  border-radius: 10px;
  width: 400px;
  text-align: center;
  background: #fff;
}

#small-modal input {
  margin: .8em auto;
  border: 1px solid #aaa;
  font-family: inherit;
  text-transform: inherit;
  font-size: inherit;
  display: block;
  width: 280px;
  padding: .4em;
}

#small-modal .formBtn {
  width: 250px;
  display: block;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  border: 0;
}

.callForm-title {
  font-size: 20px;
}

.benefits {
  min-height: 500px;
  padding-top: 100px;
  padding-bottom: 100px;
  background-color: #f3f3f3;
}

.benefits .section-title {
  margin-bottom: 60px;
  text-align: center;
}

.benefits-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  justify-content: center;
}

.benefits-item {
  box-sizing: border-box;
  margin-left: 10px;
  margin-right: 10px;
  word-wrap: break-word;
  width: calc(19.44444% - 20px);
  position: relative;
  padding-top: 100px;
  font-size: 18px;
  text-align: center;
}

.benefits-item::before {
  content: "";
  position: absolute;
  display: block;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 80px;
}

.benefits-item--cost::before {
  background: url(../images/ic-cost.svg) no-repeat center center;
  background-size: cover;
}

.benefits-item--discount::before {
  background: url(../images/ic-discount.svg) no-repeat center center;
  background-size: cover;
}

.benefits-item--time::before {
  background: url(../images/ic-time.svg) no-repeat center center;
  background-size: cover;
}

.benefits-item--delivery::before {
  background: url(../images/ic-delivery.svg) no-repeat center center;
  background-size: cover;
}

.benefits-item--country::before {
  background: url(../images/ic-country.svg) no-repeat center center;
  background-size: cover;
}

.bags .wrapper {
  position: relative;
  padding-top: 50px;
  padding-bottom: 50px;
  padding-left: 70px;
  padding-right: 70px;
}

.bags .btn {
  margin-top: 55px;
  width: 190px;
  border: 2.5px solid #141212;
}

.bags .btn-over {
  display: none;
}

.bags ul,
.bags li {
  margin: 0;
  padding: 0;
}

.bags-content {
  position: absolute;
  right: 5.7%;
  max-width: 560px;
  padding-left: 10px;
  top: 80px;
  text-align: right;
  z-index: 2;
}

.bags-title {
  margin-bottom: 8px;
}

.bags-title span {
  font-size: 31px;
}

.bags-subtitle {
  margin: 0;
  padding: 0;
  margin-bottom: 80px;
  font-size: 24px;
}

.bags-features {
  list-style-type: none;
}

.bags-features li {
  font-size: 19px;
  line-height: 54px;
}

.bags-features li::before {
  content: "";
  display: inline-block;
  width: 32px;
  height: 32px;
  vertical-align: middle;
}

.bags-features .ic-purse::before {
  background-image: url("../images/ic-purse.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain;
}

.bags-features .ic-skin::before {
  background-image: url("../images/ic-skin.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain;
}

.bags-features .ic-snow::before {
  background-image: url("../images/ic-snow.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain;
}

.bags-features .ic-bag::before {
  background-image: url("../images/ic-bag.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain;
}

.bags-features .ic-feather::before {
  background-image: url("../images/ic-feather.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain;
}

.bags-slider {
  max-width: 688px;
  position: relative;
  margin-bottom: 20px;
}

.bags-slide {
  max-width: 688px;
}

.bags .owl-theme .owl-nav {
  position: absolute;
  right: -450px;
  bottom: 70px;
}

.bags .owl-theme .owl-prev,
.bags .owl-theme .owl-next {
  font-size: 15px;
  color: #000;
  text-decoration: underline;
  background: none;
}

.bags .owl-theme .owl-prev:hover,
.bags .owl-theme .owl-prev:focus,
.bags .owl-theme .owl-prev:active,
.bags .owl-theme .owl-next:hover,
.bags .owl-theme .owl-next:focus,
.bags .owl-theme .owl-next:active {
  color: #000;
  text-decoration: underline;
  background: none;
}

.bags .owl-dots {
  position: absolute;
  left: 0;
  right: 0;
  top: 55%;
}

.bags .owl-dots .owl-dot span {
  border-color: #fff;
}

.bags .owl-dots .owl-dot span:hover {
  background-color: #fff;
}

.bags .owl-dots .owl-dot.active span {
  background-color: #fff;
}

.bags-desc {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

.bags-desc__item {
  flex: 0 1 250px;
  margin: 8px;
}

.bags-desc__img {
  margin-bottom: 16px;
}

.bags-desc__text {
  font-size: 18px;
  text-align: center;
}

.bags-desc__title {
  margin: 0;
  padding: 0;
  margin-bottom: 8px;
  font-weight: bold;
}

.bags-desc__content {
  margin: 0;
  padding: 0;
}

.conditions {
  padding-top: 80px;
  padding-bottom: 80px;
  background: #2d2741 url(../images/bkg-02.jpg) no-repeat center center;
  background-size: cover;
}

.conditions-container {
  max-width: 630px;
  margin-left: 90px;
}

.conditions .section-title {
  color: #fff;
  margin-bottom: 65px;
}

.conditions-form {
  max-width: 630px;
  padding: 30px 40px;
  border: 2px solid #fff;
  font-size: 0;
  text-align: center;
}

.form-title {
  margin-bottom: 32px;
  font-size: 24px;
  color: #fff;
}

.form-field__container {
  margin-bottom: 7px;
  border: 1.5px solid rgba(255, 255, 255, 0.87);
}

.form-field__container:last-child {
  margin-bottom: 0;
}

.form-field__container input {
  display: block;
  width: 100%;
  padding: 12px 28px;
  border: 0;
  font-size: 18px;
  text-align: left;
  color: #fff;
  background-color: transparent;
}

.form-field__container input::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: #fff;
}

.form-field__container input::-moz-placeholder {
  /* Firefox 19+ */
  color: #fff;
}

.form-field__container input:-ms-input-placeholder {
  /* IE 10+ */
  color: #fff;
}

.form-field__container input:-moz-placeholder {
  /* Firefox 18- */
  color: #fff;
}

.conditions-form .btn {
  margin-top: 20px;
  width: 270px;
  border: 0;
}

.bestseller .wrapper {
  position: relative;
  padding-top: 50px;
  padding-bottom: 50px;
  padding-left: 74px;
}

.bestseller .section-title {
  margin-bottom: 120px;
}

.bestseller .btn {
  margin-top: 100px;
  width: 190px;
  border: 2.5px solid #141212;
}

.bestseller .btn-over {
  display: none;
}

.bestseller-content {
  position: absolute;
  max-width: 280px;
  top: 160px;
  z-index: 2;
}

.bestseller-content p {
  margin: 0;
  margin-bottom: 30px;
  padding: 0;
  font-size: 20px;
}

.bestseller-content p:last-of-type {
  margin-bottom: 0;
}

.bestseller-slider {
  max-width: 754px;
  margin-left: 33.203125%;
}

.bestseller-bag {
  max-width: 754px;
}

.bestseller .owl-theme .owl-nav {
  position: absolute;
  left: -400px;
  bottom: 180px;
}

.bestseller .owl-dots {
  position: absolute;
  left: 0;
  right: 0;
  top: 55%;
}

.bestseller .owl-dots .owl-dot span {
  border-color: #fff;
}

.bestseller .owl-dots .owl-dot span:hover {
  background-color: #fff;
}

.bestseller .owl-dots .owl-dot.active span {
  background-color: #fff;
}

.bestseller .owl-theme .owl-prev,
.bestseller .owl-theme .owl-next {
  font-size: 15px;
  color: #000;
  text-decoration: underline;
  background: none;
}

.bestseller .owl-theme .owl-prev:hover,
.bestseller .owl-theme .owl-prev:focus,
.bestseller .owl-theme .owl-prev:active,
.bestseller .owl-theme .owl-next:hover,
.bestseller .owl-theme .owl-next:focus,
.bestseller .owl-theme .owl-next:active {
  color: #000;
  text-decoration: underline;
  background: none;
}

.reviews {
  padding-top: 20px;
  padding-bottom: 90px;
}

.reviews .section-title {
  margin-bottom: 60px;
  text-align: center;
}

.reviews-items {
  min-height: 400px;
}

.reviews-item {
  max-width: 450px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.reviews-item__img {
  width: 160px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
}

.reviews-item__img img {
  border-radius: 50%;
}

.reviews-item__name {
  margin-bottom: 2px;
  font-size: 22px;
  font-weight: bold;
}

.reviews-item__shop {
  margin-bottom: 16px;
  font-size: 16px;
}

.reviews-item__review {
  font-size: 18px;
  line-height: 1.3;
}

.reviews-item__review::before {
  content: "«";
}

.reviews-item__review::after {
  content: "»";
}

.page-footer {
  padding-top: 60px;
  padding-bottom: 60px;
  background-color: #231f20;
}

.page-footer .wrapper {
  padding-left: 10%;
  padding-right: 10%;
}

.page-footer .flex-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-logo:last-of-type {
  margin-bottom: 0;
}

.footer-logo__container {
  width: 200px;
  margin-left: auto;
  margin-right: auto;
}

.logo__container--small {
  width: 110px;
}

.footer-contacts {
  padding-top: 12px;
  color: #fff;
}

.footer-contact {
  position: relative;
  display: block;
  margin-bottom: 16px;
  padding-left: 50px;
  font-size: 18px;
}

.footer-contact:last-child {
  margin-bottom: 0;
}

.footer-contact::before {
  position: absolute;
  content: "";
  display: inline-block;
  left: 0;
  top: 0;
}

.footer-contact a {
  color: #fff;
  text-decoration: none;
}

.footer-contact a:hover,
.footer-contact a:focus,
.footer-contact a:active {
  text-decoration: underline;
}

.contacts-phone::before {
  top: 1px;
  left: 2px;
  width: 23px;
  height: 36px;
  background: url(../images/ic-phone.svg) no-repeat center center;
  background-size: cover;
}

.contacts-email::before {
  width: 30px;
  height: 22px;
  background: url(../images/ic-email.svg) no-repeat center center;
  background-size: cover;
}

.contacts-address::before {
  top: 1px;
  left: 2px;
  width: 24px;
  height: 32px;
  background: url(../images/ic-address.svg) no-repeat center center;
  background-size: cover;
}

.socials {
  padding-top: 16px;
  padding-bottom: 16px;
  background-color: #fff;
  font-size: 0;
  text-align: center;
}

.socials-link {
  display: inline-block;
  width: 26px;
  height: 26px;
  margin-right: 20px;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover;
}

.socials-link:last-child {
  margin-right: 0;
}

.socials-link:hover,
.socials-link:focus,
.socials-link:active {
  transform: scale(1.2, 1.2);
}

.ic-fbook {
  background-image: url(../images/social-facebook.svg);
}

.ic-instagram {
  background-image: url(../images/social-instagram.svg);
}

.ic-vk {
  background-image: url(../images/social-vk.svg);
}

.ic-twitter {
  background-image: url(../images/social-twitter.svg);
}

.ic-gplus {
  background-image: url(../images/social-googleplus.svg);
}

@media (max-width: 1600px) {
  .hero {
    height: 900px;
  }

  .hero .wrapper {
    height: 900px;
  }

  .hero-logos {
    margin-bottom: 80px;
  }

  .hero-heading {
    margin-bottom: 60px;
  }
}

@media (max-width: 1366px) {
  .hero {
    height: 700px;
  }

  .hero .wrapper {
    height: 700px;
  }

  .hero-logos {
    margin-bottom: 40px;
  }

  .hero-heading {
    margin-bottom: 40px;
  }

  .hero-heading__title {
    margin-bottom: 12px;
  }
}

@media (max-width: 1220px) {
  .bags .wrapper {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 100px;
  }

  .bags-content {
    right: 20px;
    top: 60px;
  }

  .bags-subtitle {
    margin-bottom: 40px;
  }

  .bags-features li {
    line-height: 42px;
  }

  .bestseller-content {
    top: 100px;
  }

  .bestseller-slider {
    margin-left: 26%;
  }

  .bestseller .owl-theme .owl-nav {
    left: 0;
    right: 0;
    bottom: 80px;
  }
}

@media screen and (max-width: 1200px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media (max-width: 1180px) {
  .bags .owl-theme .owl-nav {
    right: 0;
    left: 0;
  }
}

@media (max-width: 1100px) {
  .bestseller .wrapper {
    padding-left: 40px;
    padding-top: 150px;
  }

  .bestseller .section-title {
    margin-bottom: 70px;
  }

  .bestseller .btn {
    margin-top: 60px;
  }

  .bestseller-content p {
    margin-bottom: 16px;
  }

  .bestseller-slider {
    margin-left: auto;
    margin-right: auto;
  }
}

@media (max-width: 1070px) {
  .bags .wrapper {
    padding-top: 180px;
  }
}

