  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .section-title {
    font-size: 30px;
  }

  .hero-logo--1 {
    margin-right: 20px;
  }

  .hero-heading__title {
    font-size: 28px;
  }

  .hero-heading__desc {
    font-size: 20px;
  }

  .benefits {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .benefits .section-title {
    margin-bottom: 40px;
  }

  .benefits-item {
    width: calc(55.55556% - 20px);
  }

  .bags .btn {
    margin-top: 0;
  }

  .bags-title span {
    font-size: 22px;
  }

  .bags-subtitle {
    font-size: 20px;
  }

  .conditions {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .conditions .section-title {
    margin-bottom: 40px;
  }

