    padding-bottom: 50px;
  }

  .page-footer {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .page-footer .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .page-footer .flex-container {
    flex-direction: column;
  }

  .footer-logo {
    margin-bottom: 20px;
  }

  .footer-contacts {
    margin-bottom: 30px;
  }
